.square {
  width: 100px;
  height: 100px;
  background-color: #fff;
  border: 1px solid #222222;
  cursor: pointer;
  font-size: 60px;
  display: flex;
  justify-content: center;
  align-items: center;

  &:hover {
    background-color: #f0f0f0;
    transition: 0.3s all ease;
  }
}
